<template>
  <div class="container">
    <f-code spellcheck="false">
      <template v-slot:img>
        <div class="code-img"></div>
      </template>
      <template v-slot:post>
        <div v-html="compiledPost"></div>
      </template>
    </f-code>
    <textarea
      resizable="false"
      v-model="template"
      spellcheck="false"
      @input="myCompiler()"
    ></textarea>
    <div class="buttons">
      <div class="code-stylers">
        <fbutton fdata="《♤》" sentEvent="insert"> Tag styler 《♤》 </fbutton>
        <fbutton fdata="♡" sentEvent="insert"> style value ♡ </fbutton>
        <fbutton fdata="◇" sentEvent="insert"> span closing ◇ </fbutton>
      </div>
      <div class="insert">
        <fbutton fdata="<mark>marked text</mark>" sentEvent="insert">
          mark
        </fbutton>
        <fbutton fdata="<p>Paragraph start</p>" sentEvent="insert">
          paragraph
        </fbutton>
        <fbutton
          fdata="<em>&lt;-- This is a comment --&gt;</em>"
          sentEvent="insert"
        >
          Comment
        </fbutton>
        <fbutton
          fdata="<h1 class='center'> This is an h1</h1>"
          sentEvent="insert"
        >
          h1
        </fbutton>
        <fbutton fdata="<h1 class='h0'> This is an h0 </h1>" sentEvent="insert">
          h0
        </fbutton>
        <fbutton fdata="<br/>" sentEvent="insert">br</fbutton>
        <fbutton fdata="<ul><li></li></ul>" sentEvent="insert"> ul>li </fbutton>
        <fbutton
          fdata="<table><tr><td></td><td></td></tr><tr><td></td><td></td></tr><tr><td></td><td></td></tr><tr><td></td><td></td></tr><tr><td></td><td></td></tr></table>"
          sentEvent="insert"
        >
          table>tr*5>td*2
        </fbutton>
        <fbutton><label for="image">insert image</label></fbutton>
        <input type="file" id="image" @input="readURL($event)" />
      </div>

      <div class="admin">
        <fbutton @click="myCompiler()"> compile </fbutton>
        <fbutton sentEvent="refresh"> refresh Editables </fbutton>
        <fbutton sentEvent="download"> Download </fbutton>
        <fbutton sentEvent="reset" :temp="originTemplate" class="reset">
          Reset post
        </fbutton>
      </div>
    </div>
  </div>
</template>

<script>
import fCode from "../components/fCode";
import fbutton from "../components/fbutton";
export default {
  name: "post1",
  data() {
    return {
      originTemplate: `
<h1 class="center">
  <mark>&lt;/Test&gt;</mark>
</h1>
<table>
    <tr>
      <td></td>
      <td></td>
    </tr>
    <tr>
      <td></td>
      <td></td>
    </tr>
    <tr>
      <td></td>
      <td></td>
    </tr>
    <tr>
      <td></td>
      <td></td>
    </tr>
    <tr>
      <td></td>
      <td></td>
    </tr>
  </table>
<style>
h1 {}
.h0 {}
em {}
img {}
ul {}
li {}
#main-square {display: flex!important}
</style>
      `,
      template: this.originTemplate,
      compiledPost: "compile to start",
    };
  },
  components: { fCode, fbutton },
  methods: {
    myCompiler() {
      let text = document.querySelector("textarea").value;
      this.compiledPost = text
        .replace(/《/g, "<span class='purple'>&lt;")
        .replace(/》/g, "</span>&gt;</span>")
        .replace(/♤/g, "<span class='yellow'>")
        .replace(/♡/g, "<span class='ruby'>")
        .replace(/◇/g, "</span>");

      localStorage.setItem("myPost", this.compiledPost);
    },
    readURL(input) {
      if (input.currentTarget.files && input.currentTarget.files[0]) {
        let reader = new FileReader();

        reader.onload = function (e) {
          let myImg = document.createElement("img");
          myImg.setAttribute("src", e.target.result);
          document.querySelector(".code-img").appendChild(myImg);
        };

        reader.readAsDataURL(input.currentTarget.files[0]);
      }
    },
  },
  mounted() {
    document.querySelector("textarea").value = localStorage.getItem("myPost");
  },
};
</script>

<style lang="scss"></style>
